import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-biker-geo",
  templateUrl: "./biker-geo.component.html",
  styleUrls: ["./biker-geo.component.scss"]
})
export class BikerGeoComponent implements OnInit {
  loader = false;
  positionInfo: string;
  go = false;
  constructor() {}

  ngOnInit() {}

  setGeo() {
    this.loader = true;
    setTimeout(() => {
      this.loader = false;
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
          this.positionInfo =
            "Your current position is (" +
            "Latitude: " +
            position.coords.latitude +
            ", " +
            "Longitude: " +
            position.coords.longitude +
            ")";
        });
      } else {
        alert("Sorry, your browser does not support HTML5 geolocation.");
      }
    }, 2000);
  }
}
